import Vue from 'vue'
import Router from 'vue-router'
import Start from '../pages/Start'
import Game from '../pages/Game'
import Learn from '../pages/Learn'
import Continent from '../components/Continent'
import Country from '../components/Country'

Vue.use(Router)
export default new Router({
  mode: 'history',
  base: '/',
  linkActiveClass: 'active',
  routes: [    
    {
      name: 'Start',
      path: '/',
      component: Start   
    },  
    {
      name: 'Game',
      path: '/game',
      component: Game,
    },
    {
      name: 'Learn',
      path: '/learn',
      component: Learn,
      redirect: '/learn/full',
      children: [
        {
          path: '/learn/:name',
          component: Continent,
          name: 'LearnContinent',
          props: { mode: 'learn' }
        },
        {
          path: '/learn/countries',
          component: Learn,
          name: 'LearnCountry',
          children: [
            {
              path: '/learn/countries/:name',
              component: Continent,
              name: 'Country',
              props: { mode: 'learn', country: true }
            },
          ]
        }
      ]
    },
    {
      path: '*',
      component: Start
    }
  ]
})


