<template>
  <div class="start">
    <div class="left">
      <p>In this mode you have 5 attempts to guess the country. After 5 wrong attempts the game will over. If you guess right, you will play an extra game of guessing the capital of this country. Each right country and capital will give you 1 point.</p>
      <router-link to="/game" tag="button" class="btn">Game</router-link>
    </div>
    <div class="right">
      <p>In this mode you have unlimited attempts to guess the country. After 5 wrong attempts the right country will change its color a bit to easily find it.</p>
      <router-link to="/learn/full" tag="button" class="btn">Learn</router-link>
      <div class="buttons">        
        <router-link to="/learn/europe" tag="button" class="btn">Europe</router-link>
        <router-link to="/learn/asia" tag="button" class="btn">Asia</router-link>
        <router-link to="/learn/africa" tag="button" class="btn">Africa</router-link>
        <router-link to="/learn/na" tag="button" class="btn">North America</router-link>
        <router-link to="/learn/sa" tag="button" class="btn">South America</router-link>
        <router-link to="/learn/oceania" tag="button" class="btn">Oceania</router-link>
      </div>
      <p>If you want to learn regions choose a country.</p>
      <select class="country_select" v-model="selected" @change="selectCountry">
        <option value="" selected disabled hidden>Choose Country</option>
        <option v-for="(country, id) in countries" :key="id">{{ country }} </option>
      </select>
    </div>
  </div>
</template>
<script>
import router from '../router'

export default {
  data () {
    return {
      countries: ['Algeria', 'Argentina', 'Australia', 'Austria', 'Belgium', 'Brazil', 'Canada', 'China', 'Czech', 'Egypt', 'Finland', 'France', 'Germany', 'Greece', 'India', 'Ireland', 'Italy', 'Japan', 'Kazakhstan', 'Lybia', 'Mexico', 'Netherlands', 'Norway', 'Poland', 'Portugal', 'Russia', 'Spain', 'Sweden', 'Switzerland', 'Turkey', 'Ukraine', 'United Kingdom', 'USA'],
      selected: ''
    }
  },
  methods: {
    selectCountry () {
      router.push(`/learn/countries/${this.selected.replace(' ', '').toLowerCase()}`)
    }
  }
}
</script>
<style scoped>
.start {
  min-height: 100vh;
  display: flex;
}
.left, .right {
  width: 50%;
  padding: 0 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.6;
  font-size: 20px;
}
.left {
  border: 5px solid #9BFFE6;
}
.right {
  border: 5px solid #A3CCFF;
}
p {
  margin-top: 0;
}
.buttons {
  margin: 20px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.buttons > button {
  margin-bottom: 10px;
}
.country_select {
  width: 400px;
  max-width: 90%;
  height: 50px;
  padding: 0 20px;
  color: #2c3e50;
  border: 3px solid #6cb2a1;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
}
.country_select:focus {
  box-shadow: 0 0 5px 2px #6cb2a1;
}
.country_select option {
  font-size: 16px;
  padding: 10px 0;
}
@media screen and (max-width: 650px) {
  .start {
    flex-wrap: wrap;
  }
  .left, .right {
    width: 100%;
    padding: 20px;
    font-size: 18px;
  }
}
</style>


