<template>
  <div>
    <Loader v-show="showLoader" /> 
    <Map v-if="loaded" :mode="'game'" :geojson="geojson" :mapOptions="options" :stopLoader="stopLoader" />
  </div>
</template>
<script>
import Map from '../components/Map'
import Loader from '../components/Loader'

export default {
  data () {
    return {
      geojson: [],
      options: {
        zoom: 2,
        center: [40, 0],
        maxBounds: [[90, -180], [-60, 225]]
      },
      loaded: false,
      showLoader: true
    }
  },
  created () {
    this.getContinent()
  },
  methods: {
    async getContinent () {
      const res = await import('../json/full.json')
      this.geojson = [...res.features]
      console.log(this.geojson)
      this.loaded = await true
    },
    stopLoader () {
      this.showLoader = false
    }
  },
  components: {
    Map,
    Loader
  }
}
</script>


